

.centered {
  height: 100vh; /* Magic here */
  display: flex;
  justify-content: center;
  align-items: center;
}

.background {
  position: fixed;
  padding: 0;
  margin: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.parent-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 300px;
  margin-left: -4px;
}

h2 {
  padding-top: 45px;
  padding-bottom: 5px;
  border-bottom: 2px solid;
  line-height: 48px;
}

h2::after {
  content: '';
  flex: 1;
  margin-left: 1rem;
  height: 1px;
}

@media only screen
  and (min-device-width: 320px)
  and (max-device-width: 568px) {
  .parent-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 150px;
    padding-bottom: 50px;
    margin-left: -4px;
  }

  h2 {
    position: relative;
    padding-bottom: 5px;
  }

  h2::after {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    border-bottom: 1px solid #000;
    content: ""
  }
}
